.ProfileWrap {
    height: 100%;
    min-height: 45em;
    padding-top: 10em; 
}

.ProfileUpperPartWrap {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    text-align: center;
}

.Avatar {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    max-width: 100%;
    margin: auto;
    height: 300px;
    width: 300px;
    background: url(../../assets/user.png);
}

.ProfileUpperTextPartWrap {
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.ProfileUpperPartWrap span {
    font-family: "Montserrat", sans-serif;
}

.TestBtn {
    text-decoration: none;
    margin-top: 1.5em;
    border: 5px solid #000000;
    background: #000;
    color: #fff;
    font-size: 24px;
    cursor: pointer;
    -webkit-transition: .2s ease-in;
    -o-transition: .2s ease-in;
    transition: .2s ease-in;
    padding: 5px;
    border-radius: 4px;
}

.TestBtn:hover {
    border: 5px solid #DAA520;
    background: #DAA520;
    color: #fff;
}

.ProfileUpperTextPartWrap .EmailName, .ProfileUpperTextPartWrap .TestBtn {
    margin: 3em; 
}



